html,
body {
    margin: 0;
    padding: 0;
    overflow-x: hidden;
}

.App {
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.searchZone {
    width: 100vw;
    margin: auto;
    background-color: rgb(51, 51, 51);
    text-align: center;
    font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, serif;
    color: #fff;
    letter-spacing: 1px;
    padding-bottom: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.searchZone input {
    width: 75%;
    height: 50px;
    font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, serif;
    font-size: 20px;
    border: none;
}

.params {
    display: flex;
    justify-content: center;
    gap: 30px;
    margin-top: 20px;
}

.selectWrapper {
    display: flex;
    justify-content: center;
}

.resultZone {
    text-align: center;
    margin: auto;
    font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, serif;
    font-size: 25px;
    /* padding-top: 20px; */
}

.countResults {
    margin-top: 20px;
    margin-bottom: 20px;
}

.card {
    width: 200px;
    height: 280px;
    background-color: #f4f3f1;
    padding-top: 20px;
    margin: 10px;
    padding-bottom: 20px;
}

.books {
    margin: auto;
    display: flex;
    width: 70vw;
    align-items: center;
    justify-content: center;
    /* flex-direction: column; */
    flex-wrap: wrap;
}

.loadmore {
    /* padding-left: 25px; */
    /* padding-right: 25px; */
    height: 70px;
    color: white;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 10vw;
    background-color: rgb(42, 42, 42);
    margin-top: 20px;
    font-size: 25px;
    border: none;
    font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, serif;
    transition: 0.5s ease-out;
    cursor: pointer;
}

.loadmore:hover {
    border: 2px solid black;
    color: black;
    background-color: #c5c5c5;
}

.noLink {
    color: inherit;
    text-decoration: none;
    /* display: inline-block; */
}

.category {
    height: 30px;
    font-size: 13px;
    margin: 0;
    text-decoration: underline;
    /* text-decoration-color: transparent; */
    color: #858585;
    font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, serif;
}

.author {
    height: 30px;
    font-size: 13px;
    margin: 0;
    text-decoration: underline;
    color: #858585;
    font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, serif;
}

.bookName {
    letter-spacing: -1px;
    /* height: 10ch; */
    font-weight: bold;
    margin: 0;
    font-size: 15px;
    width: 128px;
    /* max-width: 20ch; */
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.textInfo {
    width: 128px;
    height: 90px;
    text-align: left;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.searchIco {
    width: 52px;
    height: 52px;
    background-color: #fff;
    background-image: url(../assets/magnifier-tool-2_icon-icons.com_56920.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 30px;
    border: none;
    transition: 200ms;
}

.searchIco:hover {
    filter: invert(100%);
}

.searchInput {
    display: flex;
    justify-content: center;
}

.noImage {
    background-color: rgb(158, 158, 158);
    width: 128px;
    height: 211px;
    /* background-color: ; */
    background-image: url(../assets/icons8-нет-изображения-50.png);
    background-repeat: no-repeat;
    background-position: center center;
}

.cardImage {
    background-color: rgb(41, 41, 41);
    margin: auto;
    width: 128px;
    height: 192px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* -webkit-box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2); */
    /* -moz-box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2); */
    box-shadow: inset;
    box-shadow: 8px 8px 7px 0px rgba(0, 0, 0, 0.2);
    margin-bottom: 10px;
}

.bookDetail-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    margin: auto;
    gap: 55px;
    height: 100%;
    /* min-height: 39vh; */
    position: relative;
}

.image-block {
    width: 50%;
    height: 100vh;
    max-height: 1000vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f4f2f3;
    /* flex-grow: 30px; */
    /* border: 1px solid grey; */
}

.image-block img {
    transform: scale(3);
    box-shadow: inset;
    box-shadow: 8px 8px 7px 0px rgba(0, 0, 0, 0.2);
    /* height: 20%; */
}

.detail-block {
    display: flex;
    flex-direction: column;
    width: 50%;
    justify-content: space-between;
}

.bookDescrption {
    width: 90%;
    text-align: start;
    font-weight: bold;
    font-size: 20px;
    letter-spacing: -1px;
    border: 1px solid grey;
    margin-top: 20px;
    padding: 20px;
    padding-right: 1vw;
    font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, serif;
}

.bookCategories {
    font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, serif;
    font-size: 20px;
    font-weight: lighter;
    text-align: start;
    margin-bottom: 50px;
}

.bookTitle {
    text-align: start;
    font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, serif;
    font-weight: bolder;
    font-size: 27px;
    margin-bottom: 20px;
    /* height: 20px; */
    /* width: 20em; */
    /* text-overflow: ellipsis; */
}

.bookAuthors {
    font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, serif;
    text-align: start;
    text-decoration: underline;
    font-weight: lighter;
    color: #474747;
    font-size: 20px;
    margin-bottom: 25px;
}

@media screen and (max-width: 400px) {
    .selectLabel {
        display: none;
    }
}

@media screen and (max-width: 1400px) {
    .image-block img {
        transform: scale(2.5);
        box-shadow: inset;
        box-shadow: 8px 8px 7px 0px rgba(0, 0, 0, 0.2);
        /* height: 20%; */
    }
}

@media screen and (max-width: 1280px) {
    .image-block img {
        transform: scale(2);
        box-shadow: inset;
        box-shadow: 8px 8px 7px 0px rgba(0, 0, 0, 0.2);
        /* height: 20%; */
    }
}

@media screen and (max-width: 900px) {
    .image-block img {
        transform: scale(1.5);
        box-shadow: inset;
        box-shadow: 8px 8px 7px 0px rgba(0, 0, 0, 0.2);
        /* height: 20%; */
    }
}

.load {
    width: 1000px;
    height: 1000px;
    background-color: red;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.loading {
    overflow: hidden;
}

.loading::after {
    content: "";
    width: 1.5em;
    height: 1.5em;
    border: 0.2em solid #ccc;
    border-top-color: #333;
    border-radius: 50%;
    animation: spin 1s infinite linear;
}

.noBooks {
    margin-top: 30vh;
}